<template>
    <div>
        <form style="width: 100%" @submit.prevent="submit">
        <CRow class="mb-3 ml-2">
            <h3>금액 청구</h3>
        </CRow>
        <CRow style="width: 80%">

            <table class="table table-striped" style="margin-left: 3%; border-collapse: collapse">
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td class="col-title">
                            <span class="table-span-margin">
                                <strong>이름</strong>
                            </span>
                    </td>
                    <td style="width: 30%">
                        <CInput v-model="custItem.name" readonly=""/>
                    </td>
                    <td class="col-title">
                            <span class="table-span-margin">
                                <strong>진료번호</strong>
                            </span>
                    </td>
                    <td style="width: 30%">
                        <CInput v-model="custItem.registNo" readonly=""/>
                    </td>
                </tr>
                <tr>
                    <td class="col-title">
                            <span class="table-span-margin">
                                <strong>미납액</strong>
                            </span>
                    </td>
                    <td style="width: 30%">
                        <CInput v-model="custItem.charge" readonly=""/>
                    </td>
                </tr>
                <tr>
                    <td class="col-title">
                        <span class="table-span-margin">
                            <strong>청구항목</strong>
                        </span>
                    </td>
                    <td style="width: 30%" colspan="2">
                        <CInput
                            placeholder=" 예) 6월 정기분"
                            v-model="item"
                        />
                    </td>
                <tr>
                    <td class="col-title">
                        <span class="table-span-margin">
                            <strong>청구액</strong>
                        </span>
                    </td>
                    <td style="width: 30%">
                            <CInput
                                placeholder="0"
                                style="float: left"
                                v-model="amount"
                            />
                        <span>원
                        </span>
                    </td>
                    <td>

                    </td>
                </tr>
                <tr>
                    <td class="col-title">
                        <span class="table-span-margin">
                            <strong>납부할 총액</strong>
                        </span>
                    </td>
                    <td style="width: 30%" colspan="2">
                        <CInput style="width: 30%"
                                v-model="totalAmount"/>
                    </td>
                </tr>
                </tbody>
            </table>

        </CRow>
        <CRow>
            <CCol>
                <div class="ml-4">
                    <input type="checkbox"
                           id="push"
                           v-model="checkedMessage"
                           true-value=
                           "강남요양병원입니다. 이번달 결제일은 25일이며, 홍길동 님의 결제액은 200,000원 입니다. 등록하신 앱에서 편리하게 분할로 결제하실수 있습니다."
                           false-value=""
                    >
                </div>
                <label class="ml-4" style="font-size: medium; font-weight:bold;" for="push">
                    알림메시지 전송
                </label>
                <br>
                <span class="ml-4 mb-2"> {{ checkedMessage }} </span>
            </CCol>
        </CRow>
        <CRow style="margin-bottom: 3%;">
            <CCol class="btn-col-margin ml-4" sm="1">
                <button label=""
                        type="button"
                        class="btn btn-info btn-block"
                        @click="submit"
                >차감하기</button>
            </CCol>
        </CRow>
        </form>
    </div>
</template>
<style>
    .btn-col-margin{
        margin-top: 5px;
        margin-left: 15px;
        margin-right: 10px;
    }
    .col-title{
        width: 20%;
    }
    .table-span-margin{
        margin-left: 10%;
    }
</style>
<script>
    import axios from "axios";

    export default {
        data() {
            return{
                custItem: null,
                custName: '',
                resgistNo: '',
                item: '',
                amount: '',
                totalAmount: '',
                checkedMessage: '',
                body: '',
                title: ''
            }
        }
        ,mounted() {
            const custId = this.$route.params.custId;

            axios.get('silver/cust/charge/'+custId)
                .then(res => {
                    this.custItem = res.data.cust;
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
        ,methods: {
            submit () {
                axios.post('/silver/cust/insertCharge', {
                    custId: this.$route.params.custId,
                    custName: this.custItem.name,
                    registNo: this.custItem.registNo,
                    item: this.item,
                    amount: this.amount
                })
                .then(data => {
                    this.$router.push({name:"paymanageupdate", params: {custId: this.$route.params.custId}});
                })
                .catch(function (error) {
                    console.log(error)
                });
            },
        }
    }
</script>
